import { collection, getDocs } from "firebase/firestore";
import  React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import Button from "react-bootstrap/Button";
import { firestore } from "./firebase";
import MyNav from "./components/Navbar"
import Player from "./addPlayers"
import NewTournament from "./newTournament";
import View from "./view";
import Home from "./home"
import Login from "./login"
import SignUp from "./signup"

import Profile from "./Profile"


import "./App.css";

function App() {
  return (
      <div>
        <MyNav/>
        <Routes>
          <Route exact path="https://chicostate.github.io/bracketmaker/" element={ <Home/> } />
          <Route exact path="https://chicostate.github.io/bracketmaker/create-tournament" element={ <NewTournament/> } />
          <Route exact path="https://chicostate.github.io/bracketmaker/create-tournament/players" element={ <Player/> } />
          <Route exact path="https://chicostate.github.io/bracketmaker/view" element={ <View/>} />

          <Route exact path="https://chicostate.github.io/bracketmaker/profile" element={ <Profile/>} />
          <Route exact path="https://chicostate.github.io/bracketmaker/login" element={ <Login/> } />
          <Route exact path="https://chicostate.github.io/bracketmaker/signup" element={<SignUp/>} />
          {/* render = {()} */}

        </Routes>
      </div>
  );
}

export default App;
